import React, { useState, useEffect } from "react";
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Button } from "@mui/material";
import { getPartTests, deletePartTest } from "../../api/api";
import PartTestForm from "./PartTestForm";

const PartTestList = () => {
    const [tests, setTests] = useState([]);
    const [selectedTest, setSelectedTest] = useState(null);
    const [showForm, setShowForm] = useState(false);

    const fetchTests = async () => {
        try {
            const res = await getPartTests();
            setTests(res.data);
        } catch (error) {
            console.error(error.response?.data || error);
            alert("Error fetching PartTests.");
        }
    };

    useEffect(() => { fetchTests(); }, []);

    const handleEdit = (test) => { setSelectedTest(test); setShowForm(true); };
    const handleDelete = async (t) => {
        if (!window.confirm("Delete this Part Test?")) return;
        try {
            await deletePartTest(t.part, t.partIssue);
            fetchTests();
        } catch (error) {
            console.error(error.response?.data || error);
            alert("Error deleting Part Test.");
        }
    };
    const handleFormClose = () => { setSelectedTest(null); setShowForm(false); fetchTests(); };

    return (
        <div>
            <h2>Part Tests</h2>
            <Button variant="contained" color="primary" onClick={() => setShowForm(true)} sx={{ mb: 2 }}>Add Part Test</Button>

            {showForm && <PartTestForm test={selectedTest} onClose={handleFormClose} onSaved={fetchTests} />}

            <TableContainer component={Paper}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Part</TableCell>
                            <TableCell>Part Issue</TableCell>
                            <TableCell>Test Type</TableCell>
                            <TableCell>Logical Function</TableCell>
                            <TableCell>Test Tag</TableCell>
                            <TableCell>Lower Limit</TableCell>
                            <TableCell>Upper Limit</TableCell>
                            <TableCell>Actions</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {tests.map(t => (
                            <TableRow key={`${t.part}-${t.partIssue}`}>
                                <TableCell>{t.part}</TableCell>
                                <TableCell>{t.partIssue}</TableCell>
                                <TableCell>{t.testType}</TableCell>
                                <TableCell>{t.logicalFunction}</TableCell>
                                <TableCell>{t.testTag}</TableCell>
                                <TableCell>{t.lowerLimitValue}</TableCell>
                                <TableCell>{t.upperLimitValue}</TableCell>
                                <TableCell>
                                    <Button size="small" variant="outlined" onClick={() => handleEdit(t)} sx={{ mr: 1 }}>Edit</Button>
                                    <Button size="small" variant="outlined" color="error" onClick={() => handleDelete(t)}>Delete</Button>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    );
};

export default PartTestList;
